<template>
  <div>
    <div class="d-flex justify-content-sm-around mt-5">
      <hr class="layui-bg-red w-25">
      <h3 class="wow animated fadeInDown" data-wow-duration="1s" data-wow-delay="0.5s">{{ $t('AppMarvelous.name') }}</h3>
      <hr class="layui-bg-red w-25">
    </div>
    <b-container fluid>
      <b-row>
        <b-col class="wow animated fadeInUp" cols="4" sm="4" data-wow-duration="1.5s" data-wow-delay="0.5s">
          <b-card class="mt-5" :title="$t('AppMarvelous.gdp')" sub-title="GDP">
            <b-card-text>
              {{ $t('AppMarvelous.gdp_describe') }}
            </b-card-text>

            <b-card-text>{{ $t('AppMarvelous.statistical_cycle')}}</b-card-text>

            <b-link href="http://www.gov.cn/shuju/index.htm" class="card-link">{{ $t('AppMarvelous.see_details') }}</b-link>
          </b-card>
        </b-col>
        <b-col class="wow animated fadeInUp" cols="8" sm="8" data-wow-duration="2.5s" data-wow-delay="0.5s">
          <x-chart id="xchart" :options="options"></x-chart>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
import XChart from '../HighCharts'
import { WOW } from 'wowjs'
export default {
  data () {
    return {
      options: {
        chart: {
          type: 'column'
        },
        title: {
          text: ''
        },
        subtitle: {
          useHTML: true,
          text: ": <a href='http://www.gov.cn/shuju/index.htm' target='_blank'></a>"
        },
        xAxis: {
          categories: [
             '20131',
             '20131-2',
             '20131-3',
             '20131-4',
             '20141',
             '20141-2',
             '20141-3',
             '20141-4',
             '20151',
             '20151-2',
             '20151-3',
             '20151-4',
             '20161',
             '20161-2',
             '20161-3',
             '20161-4',
             '20171',
             '20171-2',
             '20171-3',
             '20171-4',
             '20181',
             '20181-2',
             '20181-3',
             '20181-4',
             '20191'
          ],
          crosshair: true
        },
        yAxis: {
          min: 0,
          title: {
            text: '()'
          }
        },
        tooltip: {
          headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
 '<td style="padding:0"><b>{point.y:.1f} </b></td></tr>',
footerFormat: '</table>',
shared: true,
useHTML: true
        },
        plotOptions: {
          column: {
borderWidth: 0
}
        },
series: [{
name: '',
data: [
            129449.6,
            272968.2,
            425190.9,
            592963.2,
            140270.2,
            296192.5,
            461090.3,
            641280.6,
            150593.8,
            318468.4,
            494272.1,
            685992.9,
            160967.3,
            340846,
            530183.6,
            740060.8,
            179403.4,
            378581.1,
            588405.3,
            820754.3,
            197920,
            417215.4,
            646710.9,
            900309.5,
            213432.8,
            ]
}]
      }
    }
  },
  components: {
    XChart
  },
  mounted () {
    var wow = new WOW({
      boxClass: 'wow',
      animateClass: 'animated',
      offset: 30,
      mobile: true,
      live: false
    })
    wow.init()
  }
}
</script>

<style scoped>

</style>



