            brief: '  2018',
            link: 'http://www.iqiyi.com/w_19s62g3l9x.html'
          }
        ]
      }
    },
    mounted() {
      var wow = new WOW({
        boxClass: 'wow',
        animateClass: 'animated',
        offset: 30,
        mobile: true,
        live: false
      })
      wow.init()
    }
  }

</script>
<style scoped>
  fieldset {
    margin: 0 2px;
  }

  legend {
    border: 0;
    width: auto;
    margin-bottom: 0px;
  }

  .new-youth-flash-p {
    max-width: 20rem;
  }

</style>



